h1 {
  text-align: center;
  margin: 2vh auto;
}

.content {
  gap: 5vw;
  width: 90vw;
  margin: 3vh auto;
  font-weight: 500;
  display: flex;
  overflow: auto;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 5px 5px #00000026;
}

.left {
  flex: 3;
  overflow-y: auto;
}

.right {
  flex: 7;
  overflow-y: auto;
}

table {
  text-align: left;
  text-wrap: wrap;
  color: #000;
  table-layout: fixed;
  width: 100%;
  padding: 10px;
}

th {
  color: #fff;
  background-color: #000;
  padding: 10px;
  font-weight: bold;
}

td {
  padding: 10px;
}

tr:nth-child(2n) {
  background-color: #f3f3f3;
}

.container img {
  width: 100%;
  height: auto;
}

select {
  background-color: #f1f1f100;
  border: none;
  border-radius: 4px;
  width: 100%;
}
