h1 {
    text-align: center;
    margin: 2vh auto;
}

/* Content styling */
.content{
    overflow: auto;
    display: flex;
    width: 90vw;  
    margin: 3vh auto;
    font-weight: 500;
    gap: 5vw;
}

.container{
    padding: 20px;
    background-color: #fff;
    /* border: 1px solid #ddd; */
    border-radius: 10px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 15%);
}

.left{
    flex: 3;
    overflow-y: auto;
}

.right{
    flex: 7;
    overflow-y: auto;
}

/* Table styling */
table{
    text-align: left;
    text-wrap: wrap;
    padding: 10px;
    color: black;
    width: 100%;
    table-layout:fixed;
}

th {
    padding: 10px;
    background-color: black;
    color: white;
    font-weight: bold; /* Teks tebal untuk header */
}

td {
    padding: 10px;
}

tr:nth-child(even){
    background-color: #f3f3f3;
}

.container img{
    width: 100%;
    height: auto;
}

select{
    width: 100%;
    border: none;
    border-radius: 4px;
    background-color: #f1f1f100;
}